@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,600;0,700;1,300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.showcase{
    font-family: 'Open Sans', sans-serif;
    height: 100vh;
    background-color: rgba(25, 50, 70, 0.8);
    position: relative;
    color: white;
}

.showcase:before{
    content: '';
    background: url(/react-portfolio/static/media/Home-Background-Image.8784cb96.jpg) no-repeat center center/cover;
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    z-index: -1;
}

.container{
    
}

.showcase .showcaseInner{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align:center;
    height: 100%;
}

.showcase h1{
    font-size: 4rem;
    font-weight: 500;
    margin: 0px;
}

.showcase p{
    font-size: 1.4rem;
}

.description{
    width: 50%;
}

.projectsBtn{

}

@media(max-width:780px){
    .showcase h1{
        font-size: 3rem;
    }

    .showcase p{
        font-size: 1rem;
    }
}

@media(max-width:600px){
    .showcase h1{
        font-size: 2rem;
    }

    .showcase p{
        font-size: 0.7rem;
    }
}
.App {
  box-sizing: border-box;
  margin: 0;
  padding:0;
  text-align: center;
}

